.f-links{
    text-decoration: none;
    color: white;
}

.mettamuse-link{
    display: block;
}
.metamuse-dd{
    display: none;
}
.quick-links{
    display: block;
}
.quick-links-dd{
    display: none;
}


@media screen and(max-width:768px) {
    .mettamuse-link{
        display: none;
    }
    .metamuse-dd{
        display:block;
    }
    .quick-links{
        display: none;
    }
    .quick-links-dd{
        display: block;
    }
    
}